import { css } from '@emotion/core';
import React, { FC, ReactNode, MouseEvent, ReactElement, Fragment, useMemo, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { MenuUnfoldOutlined as IconMenuUnfoldOutlined, MenuFoldOutlined as IconMenuFoldOutlined } from '@ant-design/icons';
import ProLayout from '@ant-design/pro-layout';
import { MenuDataItem } from '@ant-design/pro-layout/es/typings';
import RightHeader from './RightHeader';
import { routes } from '../../../config/router.config';
import { useLocation, useHistory } from 'umi';
import { setAuthority, Role } from '@/utils/authority';
import GlobalStyles from '@/styles/GlobalStyles';
import { useUserRole } from '@/services/login';
import style from './BasicLayout.less';
import { backTop } from '@/utils/functions';
/* 自定义菜单项 */
function menuItemRender(props: MenuDataItem): ReactElement {

  return (
    <Link key={ props.key } to={ props?.path ?? '' }>
      { props.icon }
      <span>{ props.name }</span>
    </Link>
  );
}
const HeaderMenu = () => (
  <div style={{ padding: '16px' }}>
    <img src={ require('./images/logo.webp') } alt="logo" style={{ marginRight: '12px' }} />
    <span style={{
      color: 'white',
      fontWeight: 400,
      fontSize: 16
    }}>
      影剧综榜单
    </span>
  </div>
);


/* headers */
function collapsedButtonRender(props: any): ReactElement {
  // 点击事件
  function handleToggleClick(event: MouseEvent<HTMLElement>): void {
    props?.onCollapse?.(!props.collapsed);

    const triggerEvent: Event = document.createEvent('HTMLEvents');

    triggerEvent.initEvent('resize', true, false);
    window.dispatchEvent(triggerEvent);
  }

  return (
    <span className="ant-pro-global-header-trigger"
      role="button"
      aria-label={ props.collapsed ? '隐藏左侧菜单' : '展开左侧菜单' }
      onClick={ handleToggleClick }
    >
      { props.collapsed ? <IconMenuUnfoldOutlined /> : <IconMenuFoldOutlined /> }
    </span>
  );
}


/* 基础布局 */
const BasicLayout: FC = (props: { children?: ReactNode }) => {

  const location = useLocation();
  const history = useHistory();
  const selectedKey = location.pathname.split('/').pop() || 'theme';
  const { data } = useUserRole();
  const userRole: string[] = useMemo(() => data?.data.role || [], [data]);


  useEffect(() => {

    if (userRole.length > 0) {
      // let auth = Role.USER;

      // if (userRole.includes(Role.ADMIN)) {
      //   auth = Role.ADMIN;
      // }


      // 添加用户管理员权限
      // userRole.push(Role.SYSTEM)

      setAuthority(userRole);
    }
  }, [userRole]);

  useEffect(() => {
    const unlisten = history.listen(() => { // 监听
      backTop();
    });

    return () => unlisten();
  }, []);

  return (
    <div className={ style.wrapper }>
      <GlobalStyles />
      <ProLayout layout="side"
        title="凤凰网指数-影剧综榜单"
        menuHeaderRender={ HeaderMenu }
        route={{ routes }}
        fixedHeader={ true }
        fixSiderbar={ true }
        collapsedButtonRender={ collapsedButtonRender }
        rightContentRender={ (): ReactElement => <RightHeader userInfo={ data?.data } /> }
        menuItemRender={ menuItemRender }
        menuProps={{ selectedKeys: [selectedKey] }}
      >
        { props?.children }
      </ProLayout>
    </div>
  );
};


export default BasicLayout;