import React, { FC, useLayoutEffect, useEffect } from 'react';
import { ReactElement } from 'react';
import { Avatar, Dropdown, Menu } from 'antd';
import { ClickParam } from 'antd/es/menu';
import { LogoutOutlined as IconLogoutOutlined } from '@ant-design/icons';
import style from './rightHeader.less';
import { quitSystem } from '@/utils/functions';

interface Props { userInfo: any}

/* header右侧角色和菜单 */
const RightHeader: FC<Props> = ({ userInfo }: Props ) => {
  
  // 菜单点击
  function handleMenuClick({ key }: ClickParam): void {
    switch (key) {
      case 'quit':
        quitSystem();
        break;
    }
  }

  const menuOverlay: ReactElement = (
    <Menu onClick={ handleMenuClick }>
      <Menu.Item key="quit">
        <IconLogoutOutlined />
        退出登陆
      </Menu.Item>
    </Menu>
  );

  return (
    <div className={ style.rightHeader }>
      <Dropdown overlay={ menuOverlay }>
        <div>
          <Avatar className={ style.avatar }>
            { userInfo?.username?.toLocaleUpperCase()[0] }
          </Avatar>
          <span className={ style.name }>{ userInfo?.displayName }</span>
        </div>
      </Dropdown>
    </div>
  );
};

export default RightHeader;
