import { reloadAuthorized } from './Authorized';


// use localStorage to store the authority info, which might be sent from server in actual project.
export function getAuthority(): string[] {
  return JSON.parse(localStorage.getItem('authority') || "[]")
}


export function setAuthority(authority: string[] ): void {
  localStorage.setItem('authority', JSON.stringify(authority));
  // auto reload
  reloadAuthorized();
}


export enum Role {
  SYSTEM = 'manager_system_wingvane', // 风向标系统管理员
  WORKS = 'manager_works_wingvane', // 风向标作品管理员
  COMMENT = 'manager_comment_wingvane', // 风向标评论审核员
  EXPERT = 'manager_expert_wingvane' // 风向标专家账号管理员
}